$desktop:1200px;
$laptop:992px;
$tablet:768px;
$phone:480px;
.player{
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.time-control{
    width: 50%;
align-items: center;
 justify-content: center;
    display: flex;
    input{
        width: 100%;
        padding: 0rem;
        -webkit-appearance: none;
        background-color: transparent;
        cursor: pointer;
    }
    p{
        padding: 1rem  ;
        
    }

}
.play-control{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 30%;
    svg{
        cursor: pointer;
    }
}

input[type="range"]:focus{
    outline: none;
}
input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance: none;
    
    height: 16px;
    width: 16px;
   
}
input[type="range"]::-moz-slider-thumb{
    -webkit-appearance: none;
   border: none;
   background: transparent;
   
}
.track{
    background: lightblue;
    height: 1rem;
    width: 100%;
    border-radius: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
    


}
.animate-track{
    
    background: rgb(204, 204, 204);
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
}

@media screen and (max-width:$tablet){
    .time-control{
        width: 90%;
    }
    .play-control{
        width: 60%;
    }
}